}
#glow {
  -webkit-animation: glow 2s infinite ease;
          animation: glow 2s infinite ease;
  z-index: 2;
  position:absolute;
  height: 200px;
  width: 80vw;
  max-width: 600px;
  background:-webkit-radial-gradient(at top, rgba(255,106,0,0.5) 1%, rgba(255,0,0,0) 70%);
  background:-o-radial-gradient(at top, rgba(255,106,0,0.5) 1%, rgba(255,0,0,0) 70%);
  background:radial-gradient(at top, rgba(255,106,0,0.5) 1%, rgba(255,0,0,0) 70%);
  top: 50%;
  left: 50%;
  -webkit-transform:translateX(-50%);
      -ms-transform:translateX(-50%);
          transform:translateX(-50%);
}
.log {
  -webkit-animation: shadow 1s infinite linear;
          animation: shadow 1s infinite linear;
  position:absolute;
  width: 36vw;
  max-width: 200px;
  height: 9vw;
  max-height: 50px;
  background-color: white;
  border: 3px solid black;
  border-radius: 20px;
  top: 80px;
  left: 50%;
  -webkit-box-shadow:0px 0px 1px 3px rgba(0,0,0,0.5);
          box-shadow:0px 0px 1px 3px rgba(0,0,0,0.5);
  -webkit-filter: blur(3px);
          filter: blur(3px);
}
#log1 {
  -webkit-transform:translate(-50%,-50%)rotate(20deg);
      -ms-transform:translate(-50%,-50%)rotate(20deg);
          transform:translate(-50%,-50%)rotate(20deg);
  z-index: 6
}

#log2 {
  -webkit-transform:translate(-50%,-50%)rotate(-20deg);
      -ms-transform:translate(-50%,-50%)rotate(-20deg);
          transform:translate(-50%,-50%)rotate(-20deg);
  z-index: 10;
}

.ember {
  -webkit-animation: rotate 1s infinite;
          animation: rotate 1s infinite;
