  max-width: 80px;
  height: 15vw;
  max-height: 80px;
  border-radius: 100% 0 60% 40% / 60% 0 100% 40%;
  background:-webkit-radial-gradient(at bottom left,white 15%,yellow 1%, red 80%);
  background:-o-radial-gradient(at bottom left,white 15%,yellow 1%, red 80%);
  background:radial-gradient(at bottom left,white 15%,yellow 1%, red 80%);
  margin-top: 30px;
  top: 50%;
  left: 50%;
  position:absolute;
}
#ember1{
  -webkit-animation: ember1 1.2s infinite linear;
          animation: ember1 1.2s infinite linear;
  -webkit-transform:translate(0%,-60%)rotate(-45deg)scale(1.3);
      -ms-transform:translate(0%,-60%)rotate(-45deg)scale(1.3);
          transform:translate(0%,-60%)rotate(-45deg)scale(1.3);
  z-index: 4;
}
#ember2{
